* {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: border-box;
}
.App {
  display: grid;

  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: minmax(0px, auto);
  /* max-width: 1px; */
  margin: 0 auto;
  grid-template-areas:
    "head head head head"
    "instructor instructor instructor instructor"
    "course course course course ";
}
.App .head {
  background: #3bbced;
}

.head {
  grid-area: head;
  position: relative;
}
.head::before,
.head::after {
  content: "";
  position: absolute;
  height: 100px;
  width: 1536px;
  bottom: 0px;
  top: 390px;
  left: 0;
  background: #fff;
  transform: skewY(-3deg);
}

.navbar {
  display: flex;
  justify-content: space-evenly;
}
.navbar ul {
  display: flex;
}
.navbar ul li {
  list-style-type: none;
  margin: 25px 0 0px 10px;
}
.navbar ul li a {
  text-decoration: none;
  border: 2px solid black;
  border-radius: 10px;
  padding: 10px;
  font-size: 1.3rem;
  color: #fb2b2f;
  font-weight: 500;
}

.logo {
  height: 70px;
  width: 70px;
}
.info {
  display: flex;
  justify-content: space-evenly;
}
.intro {
  padding-top: 130px;
}
.intro h1 {
  font-size: 3rem;
}
.intro h3 {
  font-size: 2rem;
}

.earth {
  margin-top: 30px;
  height: 350px;
  width: 400px;
  z-index: 1;
}

.instructor {
  display: flex;
  grid-area: instructor;
  margin-top: 80px;
  margin-left: 50px;

  grid-gap: 140px;
}
.top_ins {
  box-shadow: 10px 11px 7px 0px #e0d4d4d1;
  border: 2px solid #e0d4d4d1;
  border-radius: 10px;
  padding: 20px;
}
.ins_pic {
  height: 200px;
  width: 200px;
}

/* Courses */
.course {
  grid-area: course;
  margin-top: 60px;
  text-align: center;
  flex-direction: column;
}
.course1 {
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
  align-content: center;
}
.course2 {
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
}
.course3 {
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
}
.course1 a,
.course2 a,
.course3 a {
  border: 1px solid #e0d4d4d1;
  padding: 10px;
  border-radius: 10px;
  text-decoration: none;
  color: #fb2b2f;
  font-weight: 600;
  box-shadow: 10px 11px 7px 0px #e0d4d4d1;
}
